.home__content{
    grid-template-columns: 80px repeat(2, 350px);
    padding: 5rem 1rem;
    column-gap: 1.2rem;
    align-items: center; 
}

.home_img{
    background: url(../Files//Photo31.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    box-shadow: inset 0 0  0 5px rgb(255 255 255/ 30%);
    order: 1;
    justify-self: center;
    height: 200px;
    width: 200px;
    animation: profile_animate 8s ease-in-out infinite 1s;
}

@keyframes profile_animate{
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50%{
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }
    100%{
        border-radius: 30% 60% 30% 70%/60% 30% 70% 40%;
    }
}

.home__social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home__social-icon{
    font-size: 1.25rem;
    color: var(--title-color);
}
.home__social-icon:hover{
    color: var(--title-color-dark);
}

.home__title{
    font-size: 55px;
    margin-bottom: var(--mb-0-25);
}
.home__subtitle{
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 2.5rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
    color: rgb(241, 183, 22);
}
.home__subtitle::before{
    content:'';
    position: absolute;
    width: 30px;
    height: 2.5px;
    background-color: black;
    left: 0;
    top:0.9rem
}

.button--flex{
 margin-left: 10px;
 padding: 7px 20px;   
}